import Layout from '../../components/Layout'
import React from 'react'
import {useSelector} from 'react-redux';
import { useEffect ,useState} from 'react';
import axios from'axios';
import { useParams } from 'react-router-dom';
import {Col,Form,Input,Row,Button,TimePicker,message} from 'antd';
import { useDispatch,  } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { showLoading,hideLoading } from "../../redux/features/alertSlice";
import moment from 'moment';

const Profile = () => {
    const {user}=useSelector(state => state.user);
    const [doctor,setDoctor] =useState(null);
    const dispatch=useDispatch();
    const navigate=useNavigate();
    const params =useParams();
   //=====update doc===
   const handleFinish= async(values)=>{
    try{
     dispatch(showLoading());
     const res =await axios.post("/api/v1/doctor/updateProfile",
     {...values,userId: user._id,
        timings:[
            moment(values.timings[0]).format('HH:mm'),
            moment(values.timings[1]).format('HH:mm'),
        ],
    
    },
     {
         headers:{
             Authorization:`Bearer ${localStorage.getItem("token")}`,
         },
     });
     dispatch(hideLoading());
     if(res.data.success){
         message.success(res.data.message);
         navigate("/");
     }else{
         message.error(res.data.success);
     }

    }catch(error){
     console.log(error)
     message.error('Something Went Wrong')
    }
 }
   //=====update doc=== 

    const getDoctorInfo= async() => {
        try{
            const res=await axios.post('/api/v1/doctor/getDoctorInfo',{
                userId:params.id},
{
    headers:{
        Authorization: `Bearer ${localStorage.getItem('token')}`
    }
  });
    if(res.data.success){
    setDoctor(res.data.data)
}
        }catch(error){
            console.log(error)
        }
    }
    useEffect(()=>{
        getDoctorInfo()
    },[]);
  return (
 <Layout>
    <h1>Manage Profile</h1>
    {doctor && (
        <Form layout='vertical' onFinish={handleFinish} className="m-3" initialValues={{
            ...doctor,timings:[
                moment(doctor.timings[0]).format('HH:mm'),
                moment(doctor.timings[1]).format("HH:mm")
            ],
        }}>
        <h4 className="">Personal Details :</h4>
  <Row gutter={20}>
      
      <Col xs={24} md={24} lg={8}>
      <Form.Item  label="First Name" name="firstName" required >
              <Input type="text" placeholder="your firstname"/>
  
  
          </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}>
  
          <Form.Item  label="LastName" name="lastName" required rules={[{required:true}]}>
              <Input type="text" placeholder="your Last  name"/>
  
          </Form.Item>
          </Col>
          <Form.Item  label="Phone No" name="phone" required rules={[{required:true}]}>
              <Input type="text" placeholder="your Phone"/>
  
          </Form.Item>
          <Col xs={24} md={24} lg={8}>
          <Form.Item  label="Email" name="email" required rules={[{required:true}]}>
              <Input type="email" placeholder="your email"/>
  
          </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}>
          <Form.Item  label="Website" name="website" >
              <Input type="text" placeholder="your Website"/>
  
          </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}>
          
          <Form.Item  label="Address" name="address" required rules={[{required:true}]} >
              <Input type="text" placeholder=" your address"/>
  
          </Form.Item>
      </Col>
  </Row>
  
        <h4 className="">Proffesional Details :</h4>
  <Row  gutter={20}>
      
      <Col xs={24} md={24} lg={8}>
      <Form.Item  label="Specialization" name="specialization" required rules={[{required:true}]}>
              <Input type="text" placeholder="your Specialization"/>
  
          </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}>
  
      
          <Form.Item  label="Experience" name="experience" required rules={[{required:true}]}>
              <Input type="text" placeholder="your Experience"/>
  
          </Form.Item>
          </Col>
          <Col xs={24} md={24} lg={8}>
          <Form.Item  label="Fees Per Consultation" name="feesPerCunsaltation" required rules={[{required:true}]}>
              <Input type="text" placeholder="Fee"/>
  
          </Form.Item>
          </Col>
         
         
        
          <Col xs={24} md={24} lg={8}>
          
          <Form.Item  label="timing" name="timings" required >
             <TimePicker.RangePicker format="HH:mm"/>
  
          </Form.Item>
    </Col>
      <Col xs={24} md={24} lg={8}></Col>
      <Col xs={24} md={24} lg={8}>
    <Form.Item>
      <Button type="primary" htmlType="submit">
        Update
      </Button>
    </Form.Item>
  </Col>
  
  </Row>
  
  
  
        </Form>
    )}
 </Layout>
  );
};

export default Profile;

