import Layout from './../components/Layout'
import React from 'react'
import {Tabs,message} from'antd';
import {useSelector,useDispatch} from "react-redux";
import { hideLoading, showLoading } from '../redux/features/alertSlice';
import axios from 'axios'
import { useNavigate } from 'react-router-dom';

const NotificationPage = () => {
    const {user} = useSelector((state)=> state.user);
    const dispatch=useDispatch()
    const navigate=useNavigate();
    const handleMarkAllRead=async()=>{
        try{
         dispatch(showLoading());
         const res=await axios.post("/api/v1/user/get-all-notification",{userId:user._id,},{
            
                headers:{
                    Authorization:`Bearer ${localStorage.getItem("token")}`,
                },
            }
         
         );
         dispatch(hideLoading)
         if(res.data.success){
            message.success(res.data.message)
         }
        }catch(error){
            dispatch(hideLoading());
console.log(error);
message.error("something went wrong")
        }
    };
    const handleDeleteAllRead=async()=>{
try{
    dispatch(showLoading());
    const res=await axios.post('/api/v1/user/delete-all-notification',{userId:user._id},{
        headers:{
            Authorization:`Bearer ${localStorage.getItem('token')}`
        }
    })
    dispatch(hideLoading());
    if(res.data.success){
        message.success(res.data.message);
    }

}catch(error){
    dispatch(hideLoading());
    console.log(error);
message.error("something went wrong")

}

    };
  return (
   <Layout>
  <h4 className="p-3 tetx-center">Notification Page</h4>
  <Tabs>
    <Tabs.TabPane tab="UnRead" key={0}>
        <div className="d-flex justify-content-end">
            <h4 className="p-2" style={{cursor:'pointer'}} onClick={handleMarkAllRead}>
Mark All Read
            </h4>
        </div>
        
            {user?.notification.map((notificationMsgs)=>(
                <div className="card" 
                style={{cursor:'pointer'}}
                >
                    <div className="card-text"  onClick={()=> navigate(notificationMsgs.onClickPath)}>
                        {notificationMsgs.message}
                    </div>
                </div>
            ))
        }
    </Tabs.TabPane>
    <Tabs.TabPane tab="Read" key={1}>
        <div className="d-flex justify-content-end">
            <h4 className="p-2 tetx-primary" style={{cursor:'pointer'}} onClick={handleDeleteAllRead}>
                Delete All Read
            </h4>
        </div>
        {user?.seennotification.map((notificationMsgs)=>(
                <div className="card" 
                style={{cursor:'pointer'}}
                >
                    <div className="card-text"  onClick={()=> navigate(notificationMsgs.onClickPath)}>
                        {notificationMsgs.message}
                    </div>
                </div>
            ))
        }
        
    </Tabs.TabPane>
  </Tabs>
   </Layout>
  )
}

export default NotificationPage

